.mentions {
  background-color: #ffffff;
  width: 100%;

  .container {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;


  .text-center {
    text-align: center;
  }

    .mention-container {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      height: 100%;

      .mention-section {
        flex:1;
        height: 250px;
        width: 33%;
        text-align: center;
        float:left;
        padding: 0 10%;
        padding-bottom: 5%;

        .title {
        padding-bottom: 10px;
        font-weight: bold;
        color: #000;
        letter-spacing: 1px;
        font-size: 15px;
        font-weight: 500;
      }
        img {
          padding-top: 10px;
          max-width: 100px;
          margin: 0 auto;
        }
      }
    }
  }
}
